/* eslint-disable no-undef */
var videoData;
var cookie;

function run() {
  chrome.runtime.onMessage.addListener(function (
    request,
    sender,
    sendResponse
  ) {
    console.log('receive message');
    sendResponse('我是content，我已收到你的消息：' + JSON.stringify(request));
    if (request.type === 'getVideoData') {
      console.log('receive message getVideoData');
      const { bvid, cid } = videoData || {};
      fetch(`https://api.bilibili.com/x/player/playurl?bvid=${bvid}&cid=${cid}&platform=html5&high_quality=1`, {
        credentials: 'same-origin'
      }).then(async (res) => {
        const result = await res.json();
        chrome.runtime.sendMessage({
          type: 'sendVideoDataFromContent',
          videoData,
          result
        }, function(response) {
          console.log('收到来自后台的回复：' + response);
        });
      });
      
    }
  });

  window.addEventListener('message', function(e) {
    if (e.data?.type === 'sendVideoDataFromInsert') {
      console.log('receive message sendVideoDataFromInsert', e.data);
      videoData = e.data?.videoData;
      cookie = e.data?.cookie;
    }
  })
}

run();


function injectScript(file, node) {
  var th = document.getElementsByTagName(node)[0];
  var s = document.createElement('script');
  s.setAttribute('type', 'text/javascript');
  s.setAttribute('src', file);
  th.appendChild(s);
}
injectScript( chrome.extension.getURL('insert.js'), 'body');